import React, { Component } from 'react';
import { Notification, Loading } from 'element-react';
import TextField from '@material-ui/core/TextField';
import SendIcon from '@material-ui/icons/Send';
import Button from '@material-ui/core/Button';
import Chip from '@material-ui/core/Chip';
import IconDone from '@material-ui/icons/CheckCircleOutlined';
import IconPending from '@material-ui/icons/HighlightOff';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Profile.css';
import EmailAPI from '../../api/EmailAPI';
import UserAPI from '../../api/UserAPI';

class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstname: '',
      lastname: '',
      username: '',
      validated: false,
      validationSent: false,
      validationLoading: false,
      validationMessage: null,
      validationError: null,
      changeEmail: true
    };
    this.inputFocus = null;
  }

  componentDidMount() {
    this.getCurrentUser();
  }

  onClickFocusEmail() {
    this.setState({ changeEmail: false }, () => this.setFocus());
  }

  getCurrentUser() {
    UserAPI.getCurrentUser()
      .then(({ firstname, lastname, username, isValidated }) => {
        this.setState({
          firstname,
          lastname,
          username,
          validated: isValidated
        });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'An error occurred while retrieving user data.'
        });
      });
  }

  setFocus() {
    this.inputFocus.focus();
  }

  sendEmail = () => {
    this.setState({ validationLoading: true });
    EmailAPI.send()
      .then(({ message }) => {
        this.setState({
          validationSent: true,
          validationLoading: false,
          validationMessage: message,
          validationError: null
        });
      })
      .catch(err => {
        this.setState({
          validationSent: false,
          validationLoading: false,
          validationMessage: null,
          validationError: err.response.data.message
        });
      });
  };

  disableInput() {
    this.setState({ changeEmail: true });
  }

  render() {
    const { classes } = this.props;
    const {
      firstname,
      lastname,
      username,
      changeEmail,
      validated,
      validationError,
      validationLoading,
      validationMessage,
      validationSent
    } = this.state;
    return (
      <div
        className={`${classes.profileGlobalContainer} ${classes.bodyContainer}`}
      >
        <div className={classes.profileContainer}>
          <div className={classes.accountSection}>
            <span className={classes.accountSectionTitle}>Account</span>
          </div>
          <form className={classes.accountForm}>
            <div className={classes.accountFormContainer}>
              <TextField
                disabled
                id="outlined-helperText"
                label="Firstname"
                value={firstname || ""}
                className={classes.textField}
                margin="normal"
                variant="outlined"
              />
              <TextField
                disabled
                id="outlined-helperText"
                label="Lastname"
                value={lastname || ""}
                className={classes.textField}
                margin="normal"
                variant="outlined"
              />
              <TextField
                disabled={changeEmail}
                id="outlined-helperText"
                label="Email"
                onBlur={() => this.disableInput()}
                inputRef={elem => this.inputFocus === elem}
                value={username || ""}
                className={classes.textField}
                margin="normal"
                variant="outlined"
              />
              {firstname !== '' && (
                <div className={classes.chipContainer}>
                  {validated ? (
                    <Chip
                      className={classes.chipDone}
                      icon={<IconDone className={classes.iconDone} />}
                      label="Account validated"
                    />
                  ) : (
                    <Chip
                      className={classes.chipPending}
                      icon={<IconPending className={classes.iconPending} />}
                      label="Account not validated"
                    />
                  )}
                </div>
              )}
              {/* Resend validation mail button */
              !validated && !validationSent && (
                <div className={classes.validateLoading}>
                  {validationLoading && <Loading className={classes.loading} />}
                  {!validationLoading && !validationError && (
                    <>
                      <p className={classes.sendText}>
                        Send the verification mail :{'  '}
                      </p>
                      <Button
                        variant="contained"
                        color="primary"
                        className={classes.buttonSend}
                        onClick={this.sendEmail}
                      >
                        Send
                        <SendIcon className={classes.sendIcon} />
                      </Button>
                    </>
                  )}
                </div>
              )}
              {/* Message once the validation email is sent */
              !validated && validationMessage && (
                <div className={classes.containerText}>
                  <strong className={classes.validateTextSuccess}>
                    {validationMessage}
                  </strong>{' '}
                  Check your spam if you don&apos;t see it in your inbox.
                </div>
              )}
              {/* Message once the validation email is sent */
              !validated && validationError && (
                <strong className={classes.validateTextError}>
                  {validationError}
                </strong>
              )}
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(Profile);
